/* global window */
import React, { memo } from 'react';
import { withStyles, useTheme } from '@material-ui/core/styles';
import { useMediaQuery, Button, Typography, Grid } from '@material-ui/core';
import PropTypes from 'prop-types';
import _get from 'lodash/get';
import { compose, withState } from 'recompose';
import Loading from 'components/Loading';
import { withMainLayout } from 'components/MainLayout';
import withRequest from 'utils/with-request';
import Session from 'utils/session';
import BlogFeedService from 'services/blog-feed';
import config from 'config';
import PostCard from './PostCard';
import styles from './styles';

const sayHello = () => {
  const viewer = Session.getUserData();
  const name = _get(viewer, 'name', '');
  const currentHour = new Date().getHours();
  if (currentHour >= 5 && currentHour < 12) {
    return `Bom dia, ${name} `;
  }
  if (currentHour >= 12 && currentHour < 18) {
    return `Boa tarde, ${name} `;
  }
  return `Boa noite, ${name} `;
};

const Home = memo(({ classes, blogFeedData, blogFeedLoading }) => {
  const theme = useTheme();
  const isSmallScreen = useMediaQuery(theme.breakpoints.down('sm'));
  const posts = _get(blogFeedData, 'feed', []);
  return (
    <React.Fragment>
      <div className={classes.headerView}>
        <Typography variant="h5">
          <b>{sayHello()}</b>
        </Typography>
        <Button
          variant="contained"
          color="primary"
          size="large"
          onClick={() => window.open(config.changelogfy, '_blank')}
          className={isSmallScreen ? classes.fab : null}
        >
          Novidades da Lobby
        </Button>
      </div>
      <br />
      <p>
        <Typography variant="h5">
          <b>Inspire-se</b>
        </Typography>
        {
          blogFeedLoading ?
            <Loading />
            :
            <Grid container direction="row" justify="flex-start" alignItems="flex-start" spacing={2}>
              {posts.map((p) => (
                <Grid item xs={12} sm={6} md={4} lg={3}>
                  <PostCard {...p} />
                </Grid>
              ))}
            </Grid>
        }
      </p>
    </React.Fragment>
  );
});

Home.propTypes = {
  classes: PropTypes.object,
  blogFeedData: PropTypes.object,
  blogFeedLoading: PropTypes.bool,
  alert: PropTypes.object,
  setAlert: PropTypes.func
};

const withBlogFeed = withRequest(BlogFeedService.getFeed, {
  name: 'blogFeed',
  options: ({ setAlert }) => ({
    onRequestError: () => {
      setAlert({
        alertMsg: 'Erro ao carregar notcias do blog',
        alertType: 'error',
        showAlert: true
      });
    }
  })
});

export default compose(
  withMainLayout,
  withStyles(styles, { withTheme: true }),
  withState('alert', 'setAlert', {
    alertMsg: '',
    alertType: '',
    showAlert: false
  }),
  withState('status', 'setStatus', ''),
  withBlogFeed
)(Home);
